@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&family=Ubuntu:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Outfit", sans-serif;
  background-color: hsl(217, 54%, 11%);
  display: inline-flex;
}

div {
  display: inline-block;
}

div .contenedor-card {
  display: flex;
  align-items: center;
  justify-content: center;
}

.card {
  height: 100%;
  background-color: hsl(216, 50%, 16%);
  border-radius: 10px;
  margin: calc(50vh - 279px) calc(50vw - 165px);
}

.contenedor {
  width: 280px;
  height: 280px;
  background-image: url(/NFT_preview_card-_componentstatic/media/image-equilibrium.88bdbc91.jpg);
  background-size: cover;
  display: flex;
  border-radius: 10px;
  margin: 0px;
  padding: 0px;
}

.fondo {
  position: absolute;
  width: 280px;
  height: 280px;
  border-radius: 10px;
}

.fondo:hover {
  background-color: hsl(178, 100%, 50%);
  opacity: 0.5;
  width: 280px;
  height: 280px;
}

.img {
  position: absolute;
}

.informacion {
  width: 100%;
  max-width: 280px;
  display: block;
}

h2 {
  color: hsl(0, 0%, 100%);
  font-weight: 600;
}

h2:hover {
  color: hsl(178, 100%, 50%);
  font-weight: 600;
}

p {
  color: hsl(215, 51%, 70%);
  font-weight: 300;
}

.adicional {
  display: flex;
  justify-content: space-between;
}

.adicional span {
  color: hsl(215, 51%, 70%);
  display: flex;
  align-items: center;
}

.adicional span img {
  margin-right: 5px;
}

.adicional .precio {
  color: hsl(178, 100%, 50%);
}
.adicional .dias {
  font-weight: 300;
}

.separador {
  height: 1px;
  width: 280px;
  background-color: hsl(215, 32%, 27%);
  margin-top: 15px;
  margin-bottom: 10px;
}

.footer {
  display: block;
}

.contenedor-footer {
  display: flex;
  align-items: center;
}

.contenedor-footer img {
  width: 35px;
  border: 1px solid white;
  border-radius: 50%;
  margin-right: 15px;
}

p {
  color: hsl(215, 51%, 70%);
  font-weight: 300;
}

span {
  color: hsl(0, 0%, 100%);
}

span:hover {
  color: hsl(178, 100%, 50%);
}

